.app {
    padding: 0 .75rem;
}

.container{
  text-align: left;
}

.container-right{
  background-color:#e1e4e7;
}

@media screen and (min-width: 768px) {

  .app {
      position: absolute;
      width: 100%;
      height: 100%;
      padding: 0;
  }

  .container{
    display:table;
    width:100%;
    height:100%;
  }

  .container-left{
    display:table-cell;
    height: 100%;
    width: 50%;
  }

  .container-left-inset {
    max-width: 430px;
    margin-left: auto;
    margin-right: 2rem;
      padding-top: 4rem;
  }

  .container-right{
    display:table-cell;
    position:relative;
    width: 100%;
    width: 50%;
    padding-right: 2rem;
    vertical-align: top;

  }

  .container-right-inset {
    max-width: 430px;
    margin-right: auto;
    margin-left: 2rem;
      padding-top: 4rem;
  }

}


