.hcard {
    margin-top: 4.75rem;
    border-bottom: 2px solid #9a9a9a;
}

.hcard-title {
    color: #95a5a6;
    text-align: right;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 300;
}

.hcard-header {
    background-color: #2c3e50;
    color: #fff;
    margin: -2rem -2rem 1rem;
    position: relative;
    height: 100px;
    line-height: 100px;
    padding-left: 2rem;
    font-size: 1.75rem;
    font-weight: 700;
}



.hcard-avatar {
    position: absolute;
    bottom: -15px;
    right: 2rem;
    padding: .25rem;
    display: block;
    height: auto;
    width: 84px;
    height: 107px;
    background-color: #fff;
    border: 1px solid #9a9a9a;
    border-bottom-width: 2px;
}

.hcard-avatar-img {
    display: block;
    max-width: 100%;
    height: auto;
    margin:0;
}

.hcard-block {
    padding: 2rem;
    background-color: #fff;
}

.hcard-row {
    border-bottom: 1px solid #dbe0e2;
    margin-bottom: .25rem;
    min-height: 1.5rem;
}

.hcard-column {
    width: 50%;
    display: inline-block;
}

.hcard-label {
    color: #34495e;
    font-size: .625rem;
    display: inline-block;
    text-transform: uppercase;
    padding-right: 1rem;
    width: 60px;
}

.hcard-label-space {
    padding-left: 60px;
}





